    <script>
    import { useRouter } from 'vue-router';
    import { ref, onMounted } from 'vue';
    import { mapState, mapGetters, mapActions} from 'vuex';
    export default {
       name: "Search",
      data() {
        return {
          buttonImageUrl: '../catalog/images/user_example.png',  // 
          userName: 'Wuhuairline' ,// ,
          searchQuery:'',   //
          activeTab: 'search',  //

          favoritePlants: [], //
          currentPage: 1, // 
          itemsPerPage: 10, // 

          contributions: [], //
          contributionPage: 1, //
          contributionItemsPerPage: 8, //

          newPlants: [], // 
          newPlantsPage: 1, // 
          newPlantsItemsPerPage: 8, // 

        };
      },
      mounted()
      {
        window.scrollTo(0,0);
        this.loadFavorites();
        this.loadContributions();
        this.loadNewPlants();
      },

      computed: {
    ...mapGetters({
      currentUser: 'getUserInfo', // 
      isAdmin: 'isAdmin',
    }),
      totalPages() {
      return Math.ceil(this.favoritePlants.length / this.itemsPerPage);
      },
      paginatedFavoritePlants() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.favoritePlants.slice(start, end);
      }, //

      contributionTotalPages() {
        return Math.ceil(this.contributions.length / this.contributionItemsPerPage);
       },

      paginatedContributions() {
      const start = (this.contributionPage - 1) * this.contributionItemsPerPage;
      const end = start + this.contributionItemsPerPage;
      return this.contributions.slice(start, end);
       },//

       newPlantsTotalPages() {
         return Math.ceil(this.newPlants.length / this.newPlantsItemsPerPage);
       },

       paginatedNewPlants() {
         const start = (this.newPlantsPage - 1) * this.newPlantsItemsPerPage;
         const end = start + this.newPlantsItemsPerPage;
         return this.newPlants.slice(start, end);
       },//
      },  //
      methods:
      {
        toggleUserMenu() {
            this.showUserMenu = !this.showUserMenu;
        },
        handleUserAvatarClick() {
          if (this.currentUser.role === 'guest') {
          this.$router.push('/login'); // guest
          } else {
          this.goToUserProfile(); // 
          }
        },

        goToUserProfile()
        {
            this.$router.push('/userprofile');
        }, //
         goToCommunity() {
         this.$router.push('/community');
        },
        goToCatalog() {
        this.$router.push('/catalog');
        },
        goHome() {
          this.$router.push('/home');
        },
        goToAdminPanel() {
          this.$router.push('/adminpanel');
        },
        GoToHome()
        {
            this.$router.push('/catalog');
        },//

        GoToContributionPage()
        {
            this.$router.push('/Contribution');
        },//

        searchDatabase()
        {
          console.log(`Searching for: ${this.searchQuery}`);
        }, //,  searchQuery 

        setActiveTab(tabName)
        {
          this.activeTab=tabName;  
        },  //

        loadFavorites() {
         this.favoritePlants = [
        { id: 1, Plantname: '' },
        { id: 2, Plantname: '' },
        { id: 3, Plantname: '' },
        { id: 4, Plantname: '' },
        { id: 5, Plantname: '' },
        { id: 6, Plantname: '' } ,
        { id: 7, Plantname: '' }, // 
        { id: 8, Plantname: '' } ,
        { id: 9, Plantname: '' } ,
        { id: 10, Plantname: '' } ,
        { id: 10, Plantname: '' } ,
        // 
      ];

      },  //


      loadContributions() {
      this.contributions = [
        { id: 1, Plantname: '', status: '' },
        { id: 2, Plantname: '', status: '' },
        { id: 3, Plantname: '', status: '' },
        { id: 4, Plantname: '', status: '' },
        { id: 5, Plantname: '', status: '' },
        { id: 6, Plantname: '', status: '' },
        { id: 7, Plantname: '', status: '' },
        { id: 8, Plantname: '', status: '' },
        { id: 9, Plantname: '', status: '' },
        { id: 10, Plantname: '', status: '' },
        // ...
      ];
      },  //

      loadNewPlants() {
      this.newPlants = [
        { id: 1, Plantname: '', updateTime: '202491' },
        { id: 2, Plantname: '', updateTime: '2024830' },
        { id: 3, Plantname: '', updateTime: '2024825' },
        { id: 4, Plantname: '', updateTime: '2024820' },
        { id: 5, Plantname: '', updateTime: '2024815' },
        { id: 6, Plantname: '', updateTime: '2024810' },
        { id: 7, Plantname: '', updateTime: '202485' },
        { id: 8, Plantname: '', updateTime: '202481' },
        { id: 9, Plantname: '', updateTime: '2024728' },
        { id: 10, Plantname: '', updateTime: '2024720' },
        // ...
      ];
    }, // 


      removeFromFavorites(plantId) {
        const plantToRemove = this.favoritePlants.find(plant => plant.id === plantId); //
        this.favoritePlants = this.favoritePlants.filter(plant => plant.id !== plantId);
         console.log(`: ${plantToRemove.Plantname}`);
       },  //


        TestPage()
        {
          this.$router.push('/information');
        }, //,

        ErrorPage()
        {
          this.$router.push('/FindError');
        },

        prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
      }, //

      nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
      }, //

      prevContributionPage() {
      if (this.contributionPage > 1) {
        this.contributionPage--;
      }
    },
    nextContributionPage() {
      if (this.contributionPage < this.contributionTotalPages) {
        this.contributionPage++;
      }
    },

    prevNewPlantsPage() {
      if (this.newPlantsPage > 1) {
        this.newPlantsPage--;
      }
    },

    nextNewPlantsPage() {
      if (this.newPlantsPage < this.newPlantsTotalPages) {
        this.newPlantsPage++;
      }
    },


      viewPlantDetail(plantId,plantName) 
      {
      this.$router.push(`/plant/${plantId}`);
      console.log(`: ${plantName}`);
      },     // ,
      }

  
    };
    </script>
    
    
    
    
