<script>
import { useRouter } from 'vue-router';
import { ref, onMounted } from 'vue';
    
export default {
  name: 'Contribution',
  data() {
    return {
      currentUser: null,
      plantData: {
        name: '',
        kingdom: '',
        phylum: '',
        class: '',
        order: '',
        family: '',
        genus: '',
        species: '',
        description: '',
        features: '',
        distribution: '',
        environment: '',
        habits: '',
        reproduction: '',
        cultivation: '',
        value: '',
        image: null,
      }, //
    };
  },
  methods: {
    //
    goBack() {
      this.$router.go(-1);
    },


    //
    onFileChange(e) {
      this.plantData.image = e.target.files[0];
    },
    




    //formData,
    async submitForm() {
      if (Object.values(this.plantData).some((field) => !field)) {
        alert('');
        return;
       } //

      try {
        const formData = new FormData();
        for (const key in this.plantData) {
          formData.append(key, this.plantData[key]);
        }//formData



            //
          // 
      console.log(':', this.plantData.name);
      console.log(':', this.plantData.kingdom);
      console.log(':', this.plantData.phylum);
      console.log(':', this.plantData.class);
      console.log(':', this.plantData.order);
      console.log(':', this.plantData.family);
      console.log(':', this.plantData.genus);
      console.log(':', this.plantData.species);
      console.log(':', this.plantData.description);
      console.log(':', this.plantData.features);
      console.log(':', this.plantData.distribution);
      console.log(':', this.plantData.environment);
      console.log(':', this.plantData.habits);
      console.log(':', this.plantData.reproduction);
      console.log(':', this.plantData.cultivation);
      console.log(':', this.plantData.value);




        // 
        const response = await fetch('/api/submitPlant', {
          method: 'POST',
          body: formData,
        });

        if (!response.ok) {
          throw new Error('');
        }

        alert('');
        this.$router.push('/success'); // 
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    },
  },
};
</script>


