<template>
  <div class="container" v-if="currentUser">
      <header class="header">
  <div class="logo">Flowers Whispering</div>
  <div class="nav-user-container">
    <nav class="nav-links">
       <nav v-if="currentUser.role === 'admin'">
         <button @click="goToAdminPanel" class="nav-button"></button>
      </nav>
      <button @click="goHome" class="nav-button"></button>
      <button @click="goToCommunity" class="nav-button"></button>
      <button @click="goToCatalog" class="nav-button"></button>
    </nav>
    <div class="user-info">
      <div class="user-avatar-wrapper">
        <img :src="currentUser.avatar" alt="User Avatar" @click="handleUserAvatarClick">
        <div class="user-info-list">
          <div v-if="currentUser.role !== 'guest'">
            <p>: {{ currentUser.username }}</p>
            <p>: {{ currentUser.email }}</p>
            <p>: {{ currentUser.userRole }}</p>
          </div>
          <div v-else>
            <p class="login-prompt"></p>
          </div>
        </div>
      </div>
    </div>
  </div>
</header>
  <!--  -->
  <video autoplay muted loop class="background-video">
    <source src="@/assets/video/background.mp4" type="video/mp4">
  </video>

    <div class="content-wrapper">
      <div class="left-panel">
        <div class="user-info-container">
          <div class="avatar-container" @click="openAvatarSelection">
            <img :src="currentUser.avatar" alt="" class="avatar" />
            <div class="avatar-overlay"></div>
            <!--  -->
            <input 
              type="file" 
              ref="fileInput" 
              @change="handleFileUpload" 
              accept="image/*" 
              style="display: none;" 
            />
          </div>
          <div class="user-details">
            <!--  -->
            <div class="form-group">
             <label for="username">&nbsp;</label>
               <div class="editable-field-container">
                <span v-if="!editing">{{ currentUser.username }}</span>
                <input 
                v-if="editing" 
                type="username" 
                v-model="userTemp.username" 
                @input="validateUsername" 
                class="fixed-input" 
                />
                <!--  -->
                <p v-if="usernameError" class="error-message">{{ usernameError }}</p> <!--  -->
              </div>
            </div>

           <div class="form-group">
            <label for="role">&nbsp;</label>
            <div class="editable-field-container">
              <span>{{ currentUser.userRole=='user'?'':'' }}</span> <!--  -->
            </div>
          </div>
           <div class="form-group">
              <label for="gender">&nbsp;</label>
              <div class="editable-field-container">
                <!--  -->
                <span v-if="!editing">{{ currentUser.gender}}</span>
                <!--  -->
                <select v-if="editing" v-model="userTemp.gender" class="fixed-input">
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
            </div>
            
            <div class="form-group">
              <label for="language"></label>
              <div class="editable-field-container">
                <!--  -->
                <span v-if="!editing">{{ currentUser.languagePreference=="zh-CN"?'':''}}</span>
                <!--  -->
                <select v-if="editing" v-model="userTemp.languagePreference" class="fixed-input">
                  <option value="zh-CN"></option>
                  <option value="EN"></option>
                </select>
              </div>
            </div>

           <div class="form-group">
             <label for="email">&nbsp;</label>
               <div class="editable-field-container">
                <span v-if="!editing">{{ currentUser.email }}</span>
                <input 
                v-if="editing" 
                type="email" 
                v-model="userTemp.email" 
                @input="validateEmail" 
                class="fixed-input" 
                />
                <!--  -->
                <p v-if="emailError" class="error-message">{{ emailError }}</p> <!--  -->
              </div>
            </div>
            <div class="form-group">
              <label for="bio">&nbsp;</label>
              <div class="editable-field-container">
                <span v-if="!editing">{{ currentUser.bio}}</span>
                <textarea v-if="editing" id="bio" v-model="userTemp.bio" class="fixed-input"></textarea>
              </div>
            </div>
          </div>
          <div class="form-group">
            <label for="registrationDate"></label>
            <div class="editable-field-container">
              <span>{{ currentUser.registrationTime }}</span> <!--  -->
            </div>
          </div>

           <!--  -->
        <div class="edit-button-container">
          <button v-if="!editing" class="edit-button" @click="editUserInfo"></button>
          
          <div v-if="editing" class="edit-actions">
            <button 
              class="confirm-button" 
              @click="confirmEdit" 
            ></button>
            <button class="cancel-button" @click="cancelEdit"></button>
            
          </div>
          
        </div>
        <button class="change-password-button" @click="showChangePasswordDialog = true"></button>
          <div class="logout-button-container">
         <button class="logout-button" @click="performLogout"></button>
         </div>
         </div>
        <!--  -->
      



      </div>
      <div class="right-panel">
        <!--  -->
     <!--  v-for  -->
  <!--  -->
  <div 
    v-for="post in posts" 
    :key="post.article_id" 
    class="post"
    @click="openPostDialog(post)"
  >
    <h3 class="post-title">{{ post.article_title }}</h3>
    <p class="post-content">{{ post.article_content }}</p>
    <p class="post-meta">
      : {{ post.published_date }} 
      <span v-if="post.article_type === 'announcement'"> | : </span>
      <span v-else> | : </span>
    </p>
    <!--  @click.stop  -->
    <button class="delete-button" @click.stop="confirmDelete(post)"></button>
  </div>

  <!--  -->
  <div v-if="showConfirmDialog" class="confirm-dialog">
    <div class="confirm-dialog-content">
      <p></p>
      <button @click="deletePost(selectedPost)"></button>
      <button @click="cancelDelete"></button>
    </div>
  </div>
 
      </div>
    </div>

     
   <!--  -->
  <div v-if="showPostDialog" class="post-dialog">
    <div class="post-dialog-content">
      <div class="post-dialog-header">
        <h3>{{ selectedPost.article_title }}</h3>
        <!--  Unicode  -->
        <button @click="closePostDialog" class="close-button">&times;</button>
      </div>
      <div class="post-dialog-body">
        <p>{{ selectedPost.article_content }}</p>
      </div>
      <div class="post-dialog-footer">
        <p></p>
        <ul>
          <li v-for="comment in comments" :key="comment.id">{{ comment.text }}</li>
        </ul>
      </div>
    </div>
  </div>


  <!--  -->
  <div v-if="showChangePasswordDialog" class="password-dialog-overlay">
    <div class="password-dialog">
      <h3></h3>
      <div class="form-group">
        <label for="old-password"></label>
        <input type="password" id="old-password" v-model="oldPassword" />
      </div>
      <div class="form-group">
        <label for="new-password"></label>
        <input type="password" id="new-password" v-model="newPassword" />
      </div>
      <div class="form-group">
        <label for="confirm-password"></label>
        <input type="password" id="confirm-password" v-model="confirmPassword" />
      </div>
      <!--  -->
      <p v-if="passwordError" class="error-message">{{ passwordError }}</p>
      <div class="dialog-actions">
        <button class="confirm-button" @click="confirmChangePassword"></button>
        <button class="cancel-button" @click="cancelChangePassword"></button>
      </div>
    </div>
  </div>
    <!--  -->
    <div v-if="showAvatarSelection" class="avatar-selection-overlay">
      <div class="avatar-selection-container">
        <h3></h3>
        <div class="avatar-grid">
          <div 
            v-for="(avatar, index) in avatarOptions" 
            :key="index" 
            :class="{'avatar-option': true, 'selected': selectedAvatar === avatar}"
            @click="selectAvatar(avatar)">
            <img :src="avatar" class="avatar-image" />
            <div v-if="selectedAvatar === avatar" class="checkmark"></div>
          </div>
        </div>
        <div class="edit-actions">
          <button class="confirm-button" @click="confirmAvatar"></button>
          <button class="cancel-button" @click="cancelAvatarSelection"></button>
        </div>
      </div>
    </div>
     <!--  -->
     <footer class="footer">
      <p class="left"><a href="contact.html"></a></p>
     <div class="center">
    <span> 2024 Flowers Whisperin&nbsp;&nbsp;&nbsp;&nbsp;</span>
    <a href="https://beian.miit.gov.cn/" target="_blank">ICP2024087175-1</a>
  </div>
      <div class="right"></div>
    </footer>
  </div> 
  <div v-else>
    ...
  </div>
</template>


<script>
import { mapState, mapGetters, mapActions } from 'vuex';

export default {
  computed: {
    ...mapGetters({
      currentUser: 'getUserInfo', // 
      isAdmin: 'isAdmin', // 
      userAvatar: 'userAvatar', //  getter 
      isAuthenticated: 'isAuthenticated' // 
    })
  },
  data() {
    return {
      posts: [{
          article_id: 1,
          article_title: "1",
          article_content: ",",
          publisher_id: 1,
          published_date: "2024-09-04",
          article_type: "article"
        },
        {
          article_id: 2,
          article_title: "",
          article_content: "",
          publisher_id: 2,
          published_date: "2024-09-03",
          article_type: "announcement"
        },{
          article_id: 1,
          article_title: "1",
          article_content: ",",
          publisher_id: 1,
          published_date: "2024-09-04",
          article_type: "article"
        },{
          article_id: 1,
          article_title: "1",
          article_content: ",",
          publisher_id: 1,
          published_date: "2024-09-04",
          article_type: "article"
        },{
          article_id: 1,
          article_title: "1",
          article_content: ",",
          publisher_id: 1,
          published_date: "2024-09-04",
          article_type: "article"
        },{
          article_id: 1,
          article_title: "1",
          article_content: ",",
          publisher_id: 1,
          published_date: "2024-09-04",
          article_type: "article"
        },{
          article_id: 1,
          article_title: "1",
          article_content: ",",
          publisher_id: 1,
          published_date: "2024-09-04",
          article_type: "article"
        },{
          article_id: 1,
          article_title: "1",
          article_content: ",",
          publisher_id: 1,
          published_date: "2024-09-04",
          article_type: "article"
        },{
          article_id: 1,
          article_title: "1",
          article_content: ",",
          publisher_id: 1,
          published_date: "2024-09-04",
          article_type: "article"
        },
      ], // 
      showChangePasswordDialog: false, // 
      passwordError: '',
      oldPassword: '',
      newPassword: '',
      confirmPassword: '',
      showPostDialog: false, // 
      showConfirmDialog: false, // 
      selectedPost: null, // 
      comments: [] ,// 


      userTemp: {},
      editing: false,
      emailError: null,
      usernameError: null,
    };
  },
  methods: {
    ...mapActions({
      logout: 'logout',
      setAvatar: 'setAvatar',
      updateUserInfo: 'updateUserInfo',
    }),
    editUserInfo() {
      this.userTemp = { ...this.currentUser };
      this.editing = true;
    },
    // 
    validateUsername() {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (emailPattern.test(this.userTemp.username)) {
        this.usernameError = '';
      } else {
        this.usernameError = null;
      }
    },
    // 
    validateEmail() {
      const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailPattern.test(this.userTemp.email)) {
        this.emailError = '';
      } else {
        this.emailError = null;
      }
    },
    
    async confirmEdit() {
      if (!this.emailError && !this.usernameError && this.editing) {
        try {
          //  Vuex  action
          await this.updateUserInfo({username:this.userTemp.username, 
                                    email:this.userTemp.email, 
                                    password:this.userTemp.password ,
                                    languagePreference:this.userTemp.languagePreference,
                                    bio:this.userTemp.bio,
                                    avatar:this.userTemp.avatar,
                                    gender:this.userTemp.gender});
          this.editing = false;
        } catch (error) {
          console.error(':', error);
          alert('');
        }
      }
    },
    toggleUserMenu() {
      this.showUserMenu = !this.showUserMenu;
    },
    handleUserAvatarClick() {
      if (this.currentUser.role === 'guest') {
        this.$router.push('/login'); // guest
      } else {
        this.goToUserProfile(); // 
      }
    },
    cancelEdit() {
      this.editing = false;  // 
      this.emailError = null;  // 
    },
    openAvatarSelection() {
      this.$refs.fileInput.click(); // 
    },
    handleFileUpload(event) {
      const file = event.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.selectedAvatar = e.target.result; // Base64selectedAvatar
          this.updateUserAvatar(); // 
        };
        reader.readAsDataURL(file); // Base64
      }
    },
    async updateUserAvatar() {
      try {
        await this.updateUserInfo({username:this.currentUser.username, 
                                    email:this.currentUser.email, 
                                    password:this.currentUser.password ,
                                    languagePreference:this.currentUser.languagePreference,
                                    bio:this.currentUser.bio,
                                    avatar:this.selectedAvatar,
                                    gender:this.currentUser.gender});
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    },
    goToCatalog() {
      this.$router.push('/catalog');
    },
    goToCommunity() {
      this.$router.push('/community');;
    },
    goToAdminPanel() {
      this.$router.push('/adminpanel');
    },
    goHome() {
      this.$router.push('/home');
    },
    performLogout() {
      this.logout(); 
      this.$router.push('/login'); // 
    },
     goToUserProfile() {
      this.$router.push('/userprofile');;
    },

    //
 // 
    confirmDelete(post) {
      this.selectedPost = post; // 
      this.showConfirmDialog = true; // 
    },
    // 
    deletePost(post) {
      // API
      this.posts = this.posts.filter(p => p.article_id !== post.article_id); // 
      this.showConfirmDialog = false; // 
      this.selectedPost = null; // 
    },
    // 
    cancelDelete() {
      this.showConfirmDialog = false; // 
      this.selectedPost = null; // 
    },
     // 
    openPostDialog(post) {
      this.selectedPost = post;
      this.comments = [ // 
        { id: 1, text: "" },
        { id: 2, text: "" },
        { id: 2, text: "" },
        { id: 2, text: "" },
        { id: 2, text: "" },
        { id: 2, text: "" },
        { id: 2, text: "" },
      ];
      this.showPostDialog = true;
    },
    // 
    closePostDialog() {
      this.showPostDialog = false;
      this.selectedPost = null;
    },
    async confirmChangePassword() {
      // 
      this.passwordError = '';

      // 
      if (this.newPassword !== this.confirmPassword) {
        this.passwordError = '';
        return;
      }
      // 
      if (this.oldPassword !== this.currentUser.password) {
        this.passwordError = '';
        return;
      }

      // 
      if (this.newPassword === this.oldPassword) {
        this.passwordError = '';
        return;
      }

      
      //  8  20 
      if (this.newPassword.length < 8 || this.newPassword.length > 20) {
        this.passwordError = ' 8  20 ';
        return;
      }
      // 
      try {
        await this.updateUserInfo({username:this.currentUser.username, 
                                    email:this.currentUser.email, 
                                    password:this.newPassword ,
                                    languagePreference:this.currentUser.languagePreference,
                                    bio:this.currentUser.bio,
                                    avatar:this.currentUser.avatar,
                                    gender:this.currentUser.gender});
        alert('');
        this.cancelChangePassword(); // 
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    },
    cancelChangePassword() {
      this.showChangePasswordDialog = false; // 
      this.oldPassword = '';
      this.newPassword = '';
      this.confirmPassword = '';
      this.passwordError = '';
    },
  }
};
</script>



