<script lang="ts">
import { defineComponent } from 'vue';
import { mapGetters, mapActions ,mapState} from 'vuex';
export default defineComponent({
  name: 'Home',
  data() {
    return {
      isUserInfoVisible: false, // 
    };
  },
  computed: {
    ...mapGetters({
      currentUser: 'getUserInfo', // 
      isAdmin: 'isAdmin',
    }),
  },
  methods: {
    ...mapActions(['logout']),
    typeEffect(el: HTMLElement, text: string, delay: number = 100, callback: () => void = () => {}) {
      let i = 0;
      const cursor = document.createElement('span');
      cursor.classList.add('typing-cursor');
      el.appendChild(cursor);

      function typing() {
        if (i < text.length) {
          el.innerHTML = text.substring(0, i + 1);
          el.appendChild(cursor); // 
          i++;
          setTimeout(typing, delay);
        } else {
          cursor.classList.add('blink'); // 
          if (callback) {
            callback(); // 
          }
        }
      }
      typing();
    },
    startTypingEffect() {
      const paragraphElement = document.querySelector('.introduction-content p') as HTMLElement;
      const text = paragraphElement.dataset.text || ''; // 
      paragraphElement.innerText = ''; // 

      this.typeEffect(paragraphElement, text, 50); // 
    },
    scrollToCards() {
      const cardsSection = document.querySelector('.feature-cards-container') as HTMLElement;
      const arrowButton = document.querySelector('.scroll-down-arrow') as HTMLElement;

      if (cardsSection) {
        cardsSection.style.display = 'flex'; //  display  flex 
        cardsSection.classList.add('visible');
        cardsSection.scrollIntoView({ behavior: 'smooth' });
      }

      if (arrowButton) {
        arrowButton.classList.add('fade-out'); 
        arrowButton.style.pointerEvents = 'none'; // 
      }
    },
    handleUserAvatarClick() {
      if (this.currentUser.userRole === 'guest') {
        this.$router.push('/login'); // guest
      } else {
        this.goToUserProfile(); // 
      }
    },
    goHome() {
      this.$router.push('/home');
    },
    goToCatalog() {
      this.$router.push('/catalog');
    },
    goToCommunity() {
      this.$router.push('/community');;
    },
    goToUserProfile() {
      this.$router.push('/userprofile');;
    },
    goToAdminPanel() {
      this.$router.push('/adminpanel');
    },
    performLogout() {
      this.logout(); 
      this.$router.push('/login'); // 
    },
    showUserInfo() {
      this.isUserInfoVisible = true;
    },
    hideUserInfo() {
      this.isUserInfoVisible = false;
    },
  },
  mounted() {
    const paragraphElement = document.querySelector('.introduction-content p') as HTMLElement;
    paragraphElement.dataset.text = paragraphElement.innerText;
    paragraphElement.innerText = ''; // 

    this.startTypingEffect(); // 
  }
});
</script>

